@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,300,600,400italic,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


body {
    background: #F2F3F9 !important;
    font-family: "Open Sans", serif;
    font-size: 16px;
}

footer{
    margin-top: 20px;
}

.row-card-education{
    margin: 10px;
}

.contenedor {
    width: 90%;
    max-width: 1000px;
    margin: auto;
    overflow: hidden;
}

.no-padding{
    padding: 0 !important;
    margin-top: 10px;
    margin-bottom: 10px;
}

.section-skills{
    margin-top: 20px;
    margin-bottom: 40px;
}
/* ----- ----- CARD ----- ----- */
.card-horizontal{
    background: #ffffff;
    border-radius: 10px;
    height: auto;
    color: #1e2b38;
    padding: 2rem;
    box-shadow: 5px 5px 2.5px grey;
    margin-bottom: 5px;
    font-family: 'Roboto', sans-serif;
}

.card-horizontal:hover{
    box-shadow: 2.5px 2.5px 1.5px grey;
    background: #F9F9F9;
}

.card-horizontal-prof{
    background: #ffffff;
    border-radius: 10px;
    height: auto;
    color: #1e2b38;
    padding: 2rem;
    box-shadow: 5px 5px 2.5px grey;
    font-family: 'Roboto', sans-serif;
}

.card-horizontal-prof:hover{
    box-shadow: 2.5px 2.5px 1.5px grey;
    background: #F9F9F9;
}

/* ----- ----- HEADER ----- ----- */
header {
    background: url(/jesusbrito.github.io/portfolio/static/media/bg.6f4d166e.jpg), #49A3D6;
    background-repeat: no-repeat;
    background-position: center;
    max-height: 600px;
}

header .menu {
    margin: 40px 0;
    text-align: right;
}

header .menu a {
    margin: 0 20px;
    color: #fff;
    padding-bottom: 9px;
    font-size: 1em;
    display: inline-block;
    text-decoration: none;
    border-bottom: 2px solid transparent;
}

header .menu a:hover {
    border-bottom: 2px solid #fff;
}

header .contenedor-texto {
    display: flex;
    justify-content: center;
    align-content: center;
    margin: 190px 0;
}

header .contenedor-texto .texto {
    border: 2px solid rgba(255, 255, 255, 0.5);
    border-radius: 3px;
    display: inline-block;
    text-align: center;
    padding: 20px 60px;
}

header .contenedor-texto .texto h1, header .contenedor-texto .texto h2 {
    color: #fff;
    font-weight: 300;
    margin: 0;
}

header .contenedor-texto .texto .nombre {
    margin-bottom: 10px;
    font-size: 50px;
    line-height: 50px;
}

header .contenedor-texto .texto .profesion {
    font-size: 25px;
    line-height: 25px;
}


/* ----- ----- MAIN ----- ----- */
.main .contenedor {
    overflow: visible;
}

.main .acerca-de {
    text-align: center;
}

.main .acerca-de .foto {
    margin-top: -60px;
}

.main .acerca-de .foto img {
    border-radius: 50%;
    vertical-align: top;
    border: 5px solid rgba(255, 255, 255, 0.2);
}

.main .acerca-de .texto {
    color: #5a5a5a;
    font-weight: 600;
    font-size: 1em;
    line-height: 30px;
    width: 80%;
    margin: 40px auto 60px auto;
}

.main .acerca-de .texto .titulo {
    font-size: 34px;
    margin-bottom: 40px;
    line-height: 34px;
    font-weight: 300;
    color: #dd820d;
    text-align: center;
}

.main .acerca-de .texto .bold {
    color: #3895ea;
    text-decoration: underline;
}


/* ----- ----- FOOTER ----- ----- */
footer .contacto {
    padding: 60px 0;
}

footer{
    background-color: #fff !important;
}

footer .contacto .titulo {
    font-size: 34px;
    line-height: 34px;
    margin-bottom: 60px;
    font-weight: 300;
    color: #3895ea;
    text-align: center;
}

footer .contacto .formulario {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

footer .contacto .formulario input[type="text"],
footer .contacto .formulario input[type="email"],
footer .contacto .formulario textarea {
    border: 2px solid #cecece;
    border-radius: 5px;
    padding: 15px 20px;
    font-size: 20px;
    color: #a4a4a4;
}

footer .contacto .formulario input[type="text"],
footer .contacto .formulario input[type="email"] {
    width: 49%;
    margin-bottom: 15px;
}

footer .contacto .formulario textarea {
    width: 100%;
    height: 100px;
    min-width: 100%;
    margin-bottom: 15px;
    min-height: 100px;
    max-height: 300px;
}

footer .contacto .formulario .boton {
    margin: auto;
    background: #3895ea;
    color: #fff;
    border: none;
    border-radius: 3px;
    padding: 15px 40px;
}

footer .contacto .formulario .boton:hover {
    background: #387bea;
}

footer .redes-sociales {
    background: #1e2b38;
    padding: 60px 0;
}

footer .redes-sociales .contenedor {
    display: flex;
    justify-content: center;
}

footer .redes-sociales .contenedor a {
    color: #fff;
    text-align: center;
    width: 100px;
    font-size: 30px;
    padding: 15px 0;
    display: block;
    margin: 0 20px;
    border-radius: 3px;
}

footer .redes-sociales .contenedor .twitter:hover {
    background: #55acee;
}

footer .redes-sociales .contenedor .whatsapp:hover {
    background: #075e54;
}

footer .redes-sociales .contenedor .youtube:hover {
    background: #cd201f;
}

footer .redes-sociales .contenedor .github:hover {
    background: #666666;
}

footer .redes-sociales .contenedor .instagram:hover {
    background: #3f729b;
}



@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

